		$version: map-get($browser-support, $browser-name)

		@return ( index($version, $browser-version) and $version <= $browser-version)
	@else
		@return null

// validate browser name
@function validate-browser($browser-name)
	$is-valid: false

	@for $i from 1 through length($browser-support)
		$is-valid: $is-valid or not not map-has-key($browser-support, $browser-name)

	@return $is-valid

